.frame{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 200vh;   //This one should be dynamic, acording to the amount of triggers
    width: 100vw;    //same for the top position of every trigger
    margin-top: 50px;
}


.frame {
    &__video {
        position: fixed;
        width: 90%;
        height: 90%;
        //background-image: url('~/img/dreams-mobile-background.svg');
        background-repeat: no-repeat;
        background-position: center;
        object-fit: cover;

        @media only screen and (min-device-width: 1023px) {
            height: 89%;
            //background-image: url('~/img/dreams-background.png');
            background-size: cover;
        }
    }

    &__hokkaido{
        position: fixed;
        width: 90%;
        height: 90%;
        //background-image: url('~/img/hokkaido-mobile-background.svg');
        background-repeat: no-repeat;
        background-position: center;
        object-fit: cover;

        @media only screen and (min-device-width: 1023px) {

            height: 89%;
            //background-image: url('~/img/hokkaido-background.png');
            background-size: cover;
        }
    }

    &__osaka{
        position: fixed;
        width: 90%;
        height: 90%;
        //background-image: url('~/img/osaka-mobile-background.svg');
        background-repeat: no-repeat;
        background-position: center;
        object-fit: cover;
        @media only screen and (min-device-width: 1023px) {
            //background-image: url('~/img/osaka-background.svg');
            background-size: cover;
        }
    }

    &__okinawa{
        opacity: 0;
        //transform: perspective(300px) translateZ(-500px);
        position: fixed;
        width: 90%;
        height: 90%;
        //background-image: url('~/img/hokkaido-mobile-background.svg');
        background-repeat: no-repeat;
        background-position: center;
        object-fit: cover;
        @media only screen and (min-device-width: 1023px) {
            //background-image: url('~/img/hokkaido-background.png');
            //background-size: cover;
        }
    }
}

.clouds-container{
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 10;

    &__video-section{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        transform: perspective(300px) translateZ(-50px);
    }

    &__hokkaido-section{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        transform: perspective(300px) translateZ(-550px);
    }

    &__osaka-section{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        transform: perspective(300px) translateZ(-550px);
    }

    &__okinawa-section{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        transform: perspective(300px) translateZ(-550px);
    }
}

.content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &__videoSection {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        //transform: perspective(200px) translateZ(0px);

        img{
            height: 100px;
            @media only screen and (min-device-width: 1023px){
                height: auto;
            }
        }
    }

    &__hokkaidoSection{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        //transform: perspective(200px) translateZ(-500px);

        img{
            height: 40px;
            @media only screen and (min-device-width: 1023px){
                height: auto;
            }
        }
    }

    &__osakaSection{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        //transform: perspective(200px) translateZ(-500px);

        img{
            height: 40px;
            @media only screen and (min-device-width: 1023px){
                height: auto;
            }
        }
    }

    &__okinawaSection{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        //transform: perspective(200px) translateZ(-500px);

        video{
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: -1;
        }

        img{
            height: 40px;
            @media only screen and (min-device-width: 1023px){
                height: auto;
            }
        }
    }
}


.description-container{
    color: white;
    text-align: center;
    font-size: 16px;
    width: 90%;

    @media only screen and (min-device-width: 1023px){
        
        width: 60%;
    }

}

