.indicators-container{
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    width: 7vw;
    background-color: white;
    border: 2px solid white;
    border-radius: 25px;
    z-index: 4;

    @media only screen and (min-device-width: 1023px) {        
        width: 4.5vw;    
        margin-top: 0px;        
    }

    &__controls{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-left: 0px;
        list-style-type: none;
        a {
            
            button {
                height: 10px;
                border-radius: 30px;
            }
        }
    }
}

.icons-container{
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    width: 7vw;
    background-color: white;
    border: 2px solid white;
    border-radius: 25px;
    right: 0;
    z-index: 4;

    @media only screen and (min-device-width: 1023px) {        
        width: 4.5vw;
        margin-top: 0px;        
    }

    &__controls{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-left: 0px;
        list-style-type: none;                

        @media only screen and (min-device-width: 1023px) {
            
            
        }

        a {
            
            button {
                height: 10px;
                border-radius: 30px;
            }
        }
    }
}


.top-bar{
    position: fixed;
    height: 4vh;
    width: 100vw;
    top: 0;
    background-color: white;
    z-index: 7;

    @media only screen and (min-device-width: 1023px) {
        display: none;
    }
}

.right-side-bar{
    position: fixed;
    height: 100vh;
    width: 7vw;
    right: 0;
    background-color: white;
    z-index: 7;

    @media only screen and (min-device-width: 1023px) {
        display: none;
    }
}
